<template>
  <v-app>
    <navigation :color="color" :flat="flat" />
    <v-main class="pt-0">
      <home />
      <!--      <about />-->
      <!--      <download />-->
      <!--      <pricing />-->
      <br />
      <contact />
    </v-main>
    <v-scale-transition>
      <v-btn
        fab
        v-show="fab"
        v-scroll="onScroll"
        dark
        fixed
        bottom
        right
        color="secondary"
        @click="toTop"
      >
        <v-icon>mdi-arrow-up</v-icon>
      </v-btn>
    </v-scale-transition>
    <foote />
  </v-app>
</template>

<style scoped>
/*.v-main {*/
/*  background-image: url("~@/assets/img/bgMain.png");*/
/*  background-attachment: fixed;*/
/*  background-position: center;*/
/*  background-size: cover;*/
/*}*/
</style>

<script>
import navigation from "./../../components/landing_page/Navigation";
import foote from "./../../components/landing_page/Footer";
import home from "./../../components/landing_page/HomeSection";
// import about from "./../../components/landing_page/AboutSection";
// import download from "./../../components/landing_page/DownloadSection";
// import pricing from "./../../components/landing_page/PricingSection";
import contact from "./../../components/landing_page/ContactSection";

export default {
  name: "App",

  components: {
    navigation,
    foote,
    home,
    contact,
  },

  data: () => ({
    fab: null,
    color: "",
    flat: null,
  }),

  created() {
    const top = window.pageYOffset || 0;
    if (top <= 60) {
      this.color = "transparent";
      this.flat = true;
    }
  },

  watch: {
    fab(value) {
      if (value) {
        this.color = "secondary";
        this.flat = false;
      } else {
        this.color = "transparent";
        this.flat = true;
      }
    },
  },

  methods: {
    onScroll(e) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 60;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
  },
};
</script>
